import React, { useContext, useState, useEffect } from 'react'
import { DesignContext } from '../App'

const NavSections = () => {
    const { newState, dispatch } = useContext(DesignContext)
    const [ gotoSection, setGotoSection ] = useState(0)

    const goto = e => { 
        // if( e === -1 && newState.sectionCount === 0 || e === 1 && newState.sectionCount === newState.section.length - 1) return
        // console.log("e ", e)
        dispatch({ type:"GOTOSECTION", value: e })
        dispatch({ type:'POPUP', value: false})
        // if(newState.sectionCount === 0 ) return setGotoSection(newState.section[0][0])
        // if(newState.sectionCount !== 0 ) setGotoSection(newState.section[newState.sectionCount + e])
        setGotoSection(gotoSection + e )
    }
        
    

    useEffect(()=>{
        const container = document.getElementById('container')
        container.style.height = `${100 * newState.section.length}vh`
        container.style.top = `${gotoSection * 100}vh`
    },[gotoSection])

    
    
    
  return (
    <div className='navSection'>
        
        {   
            gotoSection < 0 && 
            
            <div className='btn-round' onClick={ ()=> goto( 1 ) }>
                &#9650; 
            </div>
            
        }
        { 
            gotoSection > (newState.section.length - 1) * -1 &&
            
            <div className='btn-round' onClick={ ()=> goto( -1 ) }>
                &#9660;
            </div>
            
        }
    </div>
  )
}

export default NavSections